<template>
 <div>
  <div class="card"  v-for="(media, index) in results" :key="index">
    <div class="card-header">
      <h6 class="text-muted text-uppercase m-b-0">{{ getStringMedia(index) }}</h6>
    </div>
    <div class="card-body">
      <div class="results-block">
        <div class="results-table">
          <div class="results-table-row">
            <div class="results-table-column">Max Size</div>
            <div class="results-table-column">Min Size</div>
            <div class="results-table-column">CSS String</div>
            <div class="results-table-column text-right">
              <span>&nbsp;</span>
              <button class="clear-btn" title="Remove" @click.prevent="clearResults(index)">x</button>
            </div>
          </div>
          
          <div v-for="(sizes, i) in media" :key="i" class="results-table-row" :ref="'row' + index + '_' + i">
            <div class="results-table-column">{{ sizes.maxSize }}px</div>
            <div class="results-table-column">{{ sizes.minSize }}px</div>
            <div class="results-table-column">
              <pre>{{ formatCopyString(sizes, index) }}</pre>
            </div>
            <div class="results-table-column text-right">
              <div class="btn-group-custom">
              <button class="btn btn-primary btn-sm" v-clipboard="formatCopyString(sizes, index)" @success="copySuccess(index + '_' + i)" :ref="index + '_' + i">Copy</button>
              <button class="btn btn-danger btn-sm" @click.prevent="removeRow(index, i)">Remove</button>
              </div>
            </div>
          </div>
          <div class="divider">
            <hr>
          </div>
        </div>
      </div>
      
    </div>
  </div>
 </div>
</template>

<script>
export default {
  components: {
  },
  data () {
    return {
    }
  },
  props: {
    results: {
      type: Object,
      required: true
    }
  },
  methods: {
    getStringMedia (dimentionsString) {
      let parsed = this.parseWidth(dimentionsString)
      return `${parsed[0]}px to ${parsed[1]}px`
    },
    parseWidth (dimentionsString) {
      return dimentionsString.split('x')
    },
    clearResults (indexOfTableData) {
      this.$emit('on-clear', indexOfTableData)
    },
    removeRow (dimentionsString, index) {
      let rowRefName = `row${dimentionsString}_${index}`
      this.$refs[rowRefName][0].className += ' will-remove'
      this.$emit('on-remove', dimentionsString, index)
    },
    isEmptyObject (object) {
      return Object.keys(object).length === 0
    },
    formatCopyString (data, dimentionsString) {
      let parsed = this.parseWidth(dimentionsString)
      const differenceSizes = this.getDiffInSizes(data)
      const differenceWidth = this.getDiffInWidth(parsed)
      return `font-size: calc(${data.minSize}px + ${differenceSizes} * (100vw - ${parsed[0]}px) / ${differenceWidth})`
    },
    getDiffInSizes (sizes) {
      return sizes.maxSize - sizes.minSize
    },
    getDiffInWidth (dimentions) {
      return dimentions[0] - dimentions[1]
    },
    copySuccess (buttonRef) {
      this.$refs[buttonRef][0].className += ' copied'
      setTimeout(() => {
        try {
          this.$refs[buttonRef][0].classList.remove('copied')
        } catch (error) {
          console.log(error)
        }
      }, 2000)
    }
  }
}
</script>



