<template>
  <div class="container">
    <Loader :isInLoading="isInLoading"/>
    <div class="row">
      <div class="col-xs-12 col-md-4 col-lg-4 col-xl-3">
        <div class="card">
          <div class="card-body">
            <form action="#">
              <fieldset class="form-group">
                <legend>Window width:</legend>
                <!-- .row -->
                <div class="row">
                  <div class="col-6">
                    <div class="input-group">
                      <span class="input-group-addon" id="winWidthMax">
                        <label class="m-b-0" for="width_max">Max</label>
                      </span>
                      <input type="number" class="form-control" placeholder="1920" id="width_max" v-model.number="form.maxWidth">
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="input-group">
                      <span class="input-group-addon" id="winWidthMin">
                        <label class="m-b-0" for="width_min">Min</label>
                      </span>
                      <input type="number" class="form-control" placeholder="1200" id="width_min" v-model.number="form.minWidth">
                    </div>
                  </div>
                </div>
                <!-- /.row -->
              </fieldset>
              <fieldset class="form-group">
                <legend>Font-size:</legend>
                <!-- .row -->
                <div class="row">
                  <div class="col-6">
                    <div class="input-group">
                      <span class="input-group-addon" id="winWidthMax">
                        <label class="m-b-0" for="font_max">Max</label>
                      </span>
                      <input type="number" class="form-control" placeholder="28" id="font_max" v-model.number="form.maxSize">
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="input-group">
                      <span class="input-group-addon" id="winWidthMin">
                        <label class="m-b-0" for="font_min">Min</label>
                      </span>
                      <input type="number" class="form-control" placeholder="18" id="font_min" v-model.number="form.minSize">
                    </div>
                  </div>
                </div>
                <!-- /.row -->
              </fieldset>
              <div class="row">
                <div class="col">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="isreset" v-model="form.isReset">
                    <label class="form-check-label" for="isreset">Reset form after save</label>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col text-right">
                  <button type="button" class="btn btn-danger btn-sm btn-block" @click.prevent="resetForm()">Reset</button>
                </div>
                <div class="col text-right">
                  <button type="submit" class="btn btn-success btn-sm btn-block" @click.prevent="saveForm()">OK</button>
                </div>
              </div>
            </form>

          </div>
        </div>
        <!-- /card form -->
      </div>
      <div class="col-xs-12 col-md-8 col-lg-8 col-xl-9">
        <transition class="alert-wrapper" name="fade" mode="out-in">
        <div class="alert text-center" role="alert" v-if="isEmptyObject(results)">Nothing yet</div>
        </transition>

        <ResultsBlock :results="results" @on-remove="removeRow" @on-clear="clearResults"/>
      </div>
    </div>
  </div> 

</template>

<script>
/* eslint-disable */
  import ResultsBlock from '../components/ResultsBlock'
  import Loader from '../components/Loader'
  export default {
    components: {
      ResultsBlock,
      Loader
    },
    name: 'Home',
    data() {
      return {
        form: {
          minWidth: 1200,
          maxWidth: 1920,
          maxSize: 28,
          minSize: 16,
          isReset: false
        },
        results: {},
        count: 1,
        isInLoading: true
      };
    },
    mounted() {
      this.fetchResultsFormLocalStorage()
    },
    computed: {
            
      isValid() {
        for(let size in this.form) {
          if (this.form[size] === '' || this.form[size] === null) {
            return false
          }
        }
        if (this.form.minWidth >= this.form.maxWidth || this.form.minSize >= this.form.maxSize) {
          return false
        }
        return true
      },
    },
    methods: {
      removeRow (dimentionsString, index) {
        if (Object.keys(this.results[dimentionsString]).length === 1) {
          this.$delete(this.results[dimentionsString], index)
          if (!Object.keys(this.results[dimentionsString]).length) {
            this.$delete(this.results, dimentionsString)
          }
          this.updateResultsInLocalStorage()
        } else {
          setTimeout(() => {
            this.$delete(this.results[dimentionsString], index)
            if (!Object.keys(this.results[dimentionsString]).length) {
              this.$delete(this.results, dimentionsString)
            }
            this.updateResultsInLocalStorage()
          }, 500)
        }
        
      },
      
      clearResults(indexOfTableData) {
        this.$delete(this.results, indexOfTableData);
        this.updateResultsInLocalStorage()
      },
      
      fetchResultsFormLocalStorage() {
        let results
        if (window.hasOwnProperty('localStorage')) {
          try {
            results = localStorage.getItem('_results')
            if (results) {
              const parsed = JSON.parse(results)
              this.results = parsed
              this.hideLoader()
            } else {
              this.hideLoader()
            }
          } catch (err) {
            console.error(err)
          }
        } else {
          this.hideLoader()
        }
      },
      hideLoader () {
        this.$nextTick(() => {
          this.isInLoading = false
        })
      },
      updateResultsInLocalStorage() {
        if (window.hasOwnProperty('localStorage')) {
          try {
            localStorage.setItem('_results', JSON.stringify(this.results));
          } catch (err) {
            console.log(err)
          }
        }
      },
      isEmptyObject (object) {
        return Object.keys(object).length === 0
      },

      resetForm() {
        for (let key in this.form) {
          this.form[key] = key === 'isReset' ? this.form[key] : null
        }
      },
      saveForm () {
        if (!this.isValid) {
          return false
        }
        var hasProp = `${this.form.maxWidth}x${this.form.minWidth}`
        if (this.results.hasOwnProperty(hasProp)) {
        } else {
          this.$set(this.results, hasProp, {});
        }
        let name = Object.keys(this.results[hasProp])[Object.keys(this.results[hasProp]).length - 1]
        name = name === undefined ? 0 : parseInt(name) + 1
        var results = this.results[hasProp]

        this.$set(this.results[hasProp], name, {
          maxSize: this.form.maxSize,
          minSize: this.form.minSize
        })
        this.updateResultsInLocalStorage()
        if (this.form.isReset) {
          this.resetForm()
        }
      },
      isMoreThanFourSymbols (val, oldVal, formElement) {
        if (val > 9999) {
          this.form[formElement] = oldVal
        }
      }
    },
    watch: {
      'form.maxWidth': function (val, oldVal) {
        this.isMoreThanFourSymbols(val, oldVal, 'maxWidth')
      },
      'form.minWidth': function (val, oldVal) {
        this.isMoreThanFourSymbols(val, oldVal, 'minWidth')
      },
      'form.maxSize': function (val, oldVal) {
        this.isMoreThanFourSymbols(val, oldVal, 'maxSize')
      },
      'form.minSize': function (val, oldVal) {
        this.isMoreThanFourSymbols(val, oldVal, 'minSize')
      },
      
    }
  };
</script>



