<template>

  <div class="loader" :class="{ 'ready': isLoaded }" ref="loader" :style="{ 'display': hidden ? 'none' : 'block' }">
    <div class="circle first-circle"></div>
    <div class="circle second-circle"></div>
  </div>
  
</template>

<script>

export default {
  data () {
    return {
      isLoaded: false,
      hidden: false
    }
  },
  props: {
    isInLoading: {
      type: Boolean,
      required: true
    }
  },
  mounted () {
    // this.$nextTick(() => {
    //   setTimeout(() => {
    //     this.isLoaded = true
    //     this.hide()
    //   }, 2000)
    // })
  },
  methods: {
    hide () {
      setTimeout(() => {
        this.isLoaded = true
        setTimeout(() => {
          this.hidden = true
          this.$emit('hideLoader')
        }, 500)
      }, 1000)
    }
  },
  watch: {
    isInLoading (newVal) {
      if (!newVal) {
        this.hide()
      }
      this.hidden = newVal
    }
  }
}
</script>



